 .image-annotation-container {
  display: grid;
  grid-template-columns: 15% 85%; /* Adjusted column widths */
  grid-template-rows: auto 1fr; /* Adjusted row to auto */
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  height: 100vh;
}

.image-list {
  grid-area: 2 / 1 / 3 / 2; /* Adjusted grid area */
  overflow-y: auto;
}

.selected-image {
  grid-area: 2 / 2 / 3 / 3; /* Adjusted grid area */
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  touch-action: none;
}

.annotation-image {
  /* width: 100%; */
  height: 100%;
  object-fit: contain;
  pointer-events: none;
}

.labels-and-buttons {
  grid-area: 2 / 3 / 3 / 4; /* Adjusted grid area */
}

.annotation-section {
  grid-area: 2 / 2 / 3 / 3; /* Adjusted grid area */
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 10px;
}

.done-button {
  background-color: green;
  color: white;
  border: none;
}

.reset-button {
  background-color: red;
  color: white;
  border: none;
}

.image-thumbnail {
  position: relative;
}

.button-circle {
  position: absolute;
  top: 10px;
  left: 10px; /* Changed from "right: 10px;" */
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 1px solid #000;
}

.red {
  background-color: red;
}

.green {
  background-color: green;
}
